export const useAdminStore = create(
	persist(
		(set, get) => ({
			// State
			adminUser: null,
			isAuthenticated: false,
			clients: [],
			allProcesses: [],
			selectedClient: null,
			selectedProcess: null,
			currentProcessUpdates: [],
			searchTerm: "",
			searchResults: [],
			auditLogs: [],
			isLoading: false,
			error: null,

			// Statistics
			stats: {
				totalClients: 0,
				totalProcesses: 0,
				activeProcesses: 0,
				completedProcesses: 0,
			},

			// Actions
			adminLogin: async (email, password) => {
				set({ isLoading: true, error: null });

				// Simula delay de API
				await new Promise((resolve) => setTimeout(resolve, 1000));

				const admin = MOCK_ADMIN_USERS.find(
					(u) => u.email === email && u.password === password
				);

				if (admin) {
					const { password: _, ...adminWithoutPassword } = admin;
					set({
						adminUser: adminWithoutPassword,
						isAuthenticated: true,
						isLoading: false,
					});
					return { success: true };
				} else {
					set({
						error: "Email ou senha incorretos",
						isLoading: false,
					});
					return { success: false, error: "Email ou senha incorretos" };
				}
			},

			adminLogout: () => {
				set({
					adminUser: null,
					isAuthenticated: false,
					clients: [],
					allProcesses: [],
					selectedClient: null,
					selectedProcess: null,
					currentProcessUpdates: [],
					searchTerm: "",
					searchResults: [],
					error: null,
				});
			},

			fetchClients: async () => {
				set({ isLoading: true, error: null });

				// Simula delay de API
				await new Promise((resolve) => setTimeout(resolve, 800));

				set({ clients: MOCK_CLIENTS, isLoading: false });
			},

			addClient: async (clientData) => {
				set({ isLoading: true, error: null });

				// Simula delay de API
				await new Promise((resolve) => setTimeout(resolve, 1000));

				const newClient = {
					id: MOCK_CLIENTS.length + 1,
					...clientData,
					createdAt: new Date().toISOString().split("T")[0],
					processCount: 0,
				};

				MOCK_CLIENTS.push(newClient);
				set({ clients: [...MOCK_CLIENTS], isLoading: false });

				return { success: true, client: newClient };
			},

			fetchProcesses: async (clientId = null) => {
				set({ isLoading: true, error: null });

				// Simula delay de API
				await new Promise((resolve) => setTimeout(resolve, 800));

				let processes = MOCK_ALL_PROCESSES;

				if (clientId) {
					processes = processes.filter((p) => p.clientId === clientId);
				}

				// Calcula estatísticas
				const stats = {
					totalClients: MOCK_CLIENTS.length,
					totalProcesses: MOCK_ALL_PROCESSES.length,
					activeProcesses: MOCK_ALL_PROCESSES.filter(
						(p) => p.status !== "Concluído"
					).length,
					completedProcesses: MOCK_ALL_PROCESSES.filter(
						(p) => p.status === "Concluído"
					).length,
				};

				set({ allProcesses: processes, stats, isLoading: false });
			},

			addProcess: async (processData) => {
				set({ isLoading: true, error: null });

				// Simula delay de API
				await new Promise((resolve) => setTimeout(resolve, 1000));

				const client = MOCK_CLIENTS.find((c) => c.id === processData.clientId);

				const newProcess = {
					id: MOCK_ALL_PROCESSES.length + 1,
					...processData,
					clientName: client?.name || "Cliente Desconhecido",
					createdAt: new Date().toISOString().split("T")[0],
					lastUpdate: new Date().toISOString().split("T")[0],
					updatesCount: 1,
				};

				MOCK_ALL_PROCESSES.push(newProcess);

				// Atualiza contagem de processos do cliente
				if (client) {
					client.processCount += 1;
				}

				set({ allProcesses: [...MOCK_ALL_PROCESSES], isLoading: false });

				return { success: true, process: newProcess };
			},

			selectProcess: async (processId) => {
				set({ isLoading: true, error: null });

				// Simula delay de API
				await new Promise((resolve) => setTimeout(resolve, 600));

				const process = MOCK_ALL_PROCESSES.find((p) => p.id === processId);

				if (process) {
					set({ selectedProcess: process, isLoading: false });
				} else {
					set({ error: "Processo não encontrado", isLoading: false });
				}
			},

			addProcessUpdate: async (processId, updateData) => {
				set({ isLoading: true, error: null });

				// Simula delay de API
				await new Promise((resolve) => setTimeout(resolve, 1000));

				const adminUser = get().adminUser;

				const newUpdate = {
					id: Date.now(),
					...updateData,
					createdBy: adminUser?.name || "Desconhecido",
					createdAt: new Date().toISOString(),
				};

				// Adiciona log de auditoria
				const auditLog = {
					id: MOCK_AUDIT_LOGS.length + 1,
					processId,
					action:
						updateData.status === "published"
							? "Publicação de andamento"
							: "Criação de rascunho",
					description: `Andamento '${updateData.title}' ${
						updateData.status === "published" ? "publicado" : "salvo como rascunho"
					}`,
					user: adminUser?.name || "Desconhecido",
					timestamp: new Date().toISOString(),
				};

				MOCK_AUDIT_LOGS.push(auditLog);

				// Atualiza o processo
				const processIndex = MOCK_ALL_PROCESSES.findIndex((p) => p.id === processId);
				if (processIndex !== -1) {
					MOCK_ALL_PROCESSES[processIndex].lastUpdate = new Date()
						.toISOString()
						.split("T")[0];
					MOCK_ALL_PROCESSES[processIndex].updatesCount += 1;
				}

				set({ isLoading: false });

				return { success: true, update: newUpdate };
			},

			fetchProcessAuditTrail: async (processId) => {
				set({ isLoading: true, error: null });

				// Simula delay de API
				await new Promise((resolve) => setTimeout(resolve, 600));

				const logs = MOCK_AUDIT_LOGS.filter((log) => log.processId === processId);

				set({ auditLogs: logs, isLoading: false });
			},

			search: async (term) => {
				set({ isLoading: true, error: null, searchTerm: term });

				// Simula delay de API
				await new Promise((resolve) => setTimeout(resolve, 500));

				if (!term || term.trim() === "") {
					set({ searchResults: [], isLoading: false });
					return;
				}

				const lowerTerm = term.toLowerCase();

				// Busca por número do processo ou palavras-chave no título/descrição
				const results = MOCK_ALL_PROCESSES.filter(
					(p) =>
						p.processNumber.toLowerCase().includes(lowerTerm) ||
						p.title.toLowerCase().includes(lowerTerm) ||
						p.description.toLowerCase().includes(lowerTerm) ||
						p.clientName.toLowerCase().includes(lowerTerm)
				);

				set({ searchResults: results, isLoading: false });
			},

			clearSearch: () => {
				set({ searchTerm: "", searchResults: [] });
			},

			// Funções auxiliares
			getClientById: (clientId) => {
				return get().clients.find((c) => c.id === clientId);
			},

			getProcessesByClient: (clientId) => {
				return get().allProcesses.filter((p) => p.clientId === clientId);
			},
		}),
		{
			name: "admin-storage",
			partialize: (state) => ({
				adminUser: state.adminUser,
				isAuthenticated: state.isAuthenticated,
			}),
		}
	)
);

